<template>
    <div class="dialog">
        <el-dialog
            :title="dialog.title"
            :visible.sync="dialog.show"
            :close-on-click-modal="false"
            :close-on-press-escape="false"
            :modal-append-to-body="false"
        >
            <div class="form">
                <el-form
                    ref="form"
                    :model="formData"
                    :rules="form_rules"
                    label-width="120px"
                    style="margin:10px;width:auto;"    
                >
                    <el-form-item prop="lab_name" label="">
                        <el-input type="describe" v-model="formData.lab_name"></el-input>
                    </el-form-item>
                   <el-form-item prop="info" label=":">
                        <el-input type="info" v-model="formData.info"></el-input>
                    </el-form-item>

                    <el-form-item prop="picture" label=":">
                        <el-input type="picture" v-model="formData.picture"></el-input>
                    </el-form-item>

                    <el-form-item prop="capacity" label=":">
                        <el-input type="capacity" v-model="formData.capacity"></el-input>
                    </el-form-item>

                    <el-form-item prop="owner_1" label=":">
                        <el-input type="owner_1" v-model="formData.owner_1"></el-input>
                    </el-form-item>

                    <el-form-item prop="owner_2" label=":">
                        <el-input type="owner_2" v-model="formData.owner_2"></el-input>
                    </el-form-item>

                    <el-form-item class="text_right">
                        <el-button @click="dialog.show = false"></el-button>
                        <el-button @click="onSubmit('form')"></el-button>
                    </el-form-item>
                </el-form>
            </div>
        </el-dialog>
    </div>
</template>
<script>
export default {
    name:"labdialog",
    data(){
        return {
            form_rules:{
                describe:[
                    {
                        required:true,
                        message:"",
                        trigger:'blur'
                    }
                ],
                income:[
                    {
                        required:true,
                        message:"",
                        trigger:"blur"
                    }
                ]
            }
        }
    },
    props:{
        dialog: Object,
        formData: Object
    },
    methods:{
        onSubmit(form){
            this.$refs[form].validate(valid =>{
                if(valid){
                    const url = this.dialog.option == "add" ? "add" : `update/${this.formData.id}`;
                    this.$axios.post(`/api/labs/${url}`,this.formData)
                    .then(res=>{
                        this.$message({
                            message:"",
                            type:"success"
                        });
                        // dialog
                        this.dialog.show = false;
                        this.$emit('update');
                    })
                }
            })
            console.log("");
            this.dialog.show = false;
        }
    }
};
</script>
<style scoped>

</style>