<template>
    <div class="fillContainer">
        <div>
            <el-form :inline="true" ref="add_data">
                <el-form-item class="btnRight">
                    <el-button type="primary" size="big" icon="view" 
                    v-if="user.identity == ''"
                    @click="handleAdd()" ></el-button>
                </el-form-item>
            </el-form>
        </div>
        <div class="table_container">
            <el-table
                v-if="tableData.length > 0"
                :data="tableData"
                max-height="450"
                border
                style="width: 100%">
                <el-table-column
                    prop="lab_name"
                    align="center"
                    label=""
                    width="150">
                </el-table-column>
                <el-table-column
                    prop="info"
                    align="center"
                    label=""
                    width="350">
                </el-table-column>
                <el-table-column
                    prop="picture"
                    align="center"
                    label=""
                    width="200">
                </el-table-column>
                <el-table-column
                    prop="capacity"
                    align="center"
                    label=""
                    width="200">
                </el-table-column>
                <el-table-column
                    prop="owner_1"
                    align="center"
                    label=""
                    width="170">
                </el-table-column>
                <el-table-column
                    prop="owner_2"
                    align="center"
                    label=""
                    width="170">
                </el-table-column>
                <el-table-column
                    label=""
                    align="center"
                    prop="operation"
                    width="220">
                <template slot-scope="scope">
                    <el-button
                    size="small"
                    type="warning"
                    icon="edit"
                    v-if="user.identity === ''"
                    @click="handleEdit(scope.$index, scope.row)"></el-button>
                    <el-button
                    size="small"
                    type="danger"
                    icon="delete"
                    v-if="user.identity === ''"
                    @click="handleDelete(scope.$index, scope.row)"></el-button>
                </template>
                </el-table-column>
            </el-table>
            <!---->
            <el-row  :span="24" v-if="tableData.length > 0">
                <div class="pagination">
                    <el-pagination
                        @size-change="handleSizeChange"
                        @current-change="handleCurrentChange"
                        :current-page.sync="paginations.page_index"
                        :page-sizes="paginations.page_sizes"
                        :page-size="paginations.page_size"
                        :layout="paginations.layout"
                        :total="paginations.total">
                    </el-pagination>
                </div>
            </el-row>
            
        </div>
        <LabDialog :dialog="dialog" :formData="formData" @update="getLabList"></LabDialog>
    </div>
</template>

<script>
// Dialog
import LabDialog from '../components/LabDialog'

export default {
    name:"lablist",
    data(){
        return {
            // 
            filterTableData:[],
            // 
            paginations:{
                page_index: 1,// 
                total: 0,//
                page_size:5,//
                page_sizes:[5,10,15,20],//
                layout:"total,sizes,prev,pager,next,jumper" //
            },
            //
            formData:{
                id:"",
                lab_name:"",
                owner_1:"",
                owner_2:"",
                picture:"",
                info:"",
                capacity:""
            },
            tableData:[],
            allTableData:[],
            // dialog
            dialog:{
                show:false,
                title:'',
                option:'edit'
            }
        };
    },
    computed:{
        user(){
            return this.$store.getters.user;
        }
    },
    created(){
        this.getLabList();
    },
    methods:{
        getLabList(){
            this.$axios.get("/api/labs")
            .then(res=>{
                this.allTableData = res.data;
                this.filterTableData = res.data;
                // 
                this.setPaginations();
            }).catch(err=>{console.log(err)});
        },
        handleEdit(index,row){
            this.dialog = {
                show : true,
                title: "",
                option: "edit"
            };
            this.formData = {
                lab_name:row.lab_name,
                info:row.info,
                picture:row.picture,
                capacity:row.capacity,
                owner_1:row.owner_1,
                owner_2:row.owner_2,
                id:row._id
            };
        },
        handleDelete(index,row){
            this.$axios.delete(`/api/labs/delete/${row._id}`)
            .then(res=>{
                this.$message('!');
                this.getLabList();
            });
        },handleAdd(){
            this.dialog = {
                show : true,
                title: "",
                option: "add"
            };
            this.formData = {
                lab_name:"",
                info:"",
                picture:"",
                capacity:"",
                owner_1:"",
                owner_2:"",
                id:""
            };
        },
        handleSizeChange(page_size){
            // size
            this.paginations.page_index = 1;
            this.paginations.page_size = page_size;
            this.tableData = this.allTableData.filter((item,index) => {
              return index < page_size   
            })
        },
        handleCurrentChange(page){
            //  =  *  - 1
            let index = this.paginations.page_size * (page - 1);
            //  + 1 
            let nums = this.paginations.page_size * page;
            // 
            let tables = [];
            //  [inde,nums),
            for (let i = index; i < nums; i++){
                if(this.allTableData[i]){
                    tables.push(this.allTableData[i]);
                }
            }
            // 
            this.tableData = tables;
        },
        setPaginations(){
            // 
            this.paginations.total = this.allTableData.length;//
            this.paginations.page_index = 1;//
            this.paginations.page_size = 5;//
            // 
            this.tableData = this.allTableData.filter((item,index) => {
              return index < this.paginations.page_size;    
            })
        }
    },
    components:{
        LabDialog
    }
}
</script>
<style scoped>
.fillContainer{
    width: 90%;
    height: 100%;
    padding-left: 100px;
    padding-top: 50px;
    box-sizing:border-box;
}

.btnRight{
    float: right;
}

.pagination{
    text-align: right;
    margin-top: 10px;
}
</style>