<template>
    <div class="fillContainer">
        <div class="table_container">
            <el-table
                v-if="tableData.length > 0"
                :data="tableData"
                max-height="450"
                border
                style="width: 100%">
                <el-table-column
                    prop="lab_name"
                    align="center"
                    label=""
                    width="150">
                </el-table-column>
                <el-table-column
                    prop="book_date"
                    align="center"
                    label=""
                    width="350">
                </el-table-column>
                <el-table-column
                    prop="book_user"
                    align="center"
                    label=""
                    width="200">
                </el-table-column>
                <el-table-column
                    prop="part"
                    align="center"
                    label=""
                    width="200">
                </el-table-column>
                <el-table-column
                    label=""
                    align="center"
                    prop="operation"
                    width="220">
                <template slot-scope="scope">
                    <el-button
                    size="small"
                    type="warning"
                    icon="edit"
                    v-if="user.identity == ''"
                    @click="handleAgree(scope.$index, scope.row)"></el-button>
                    <el-button
                    size="small"
                    type="danger"
                    icon="delete"
                    v-if="user.identity == ''"
                    @click="handleRefuse(scope.$index, scope.row)"></el-button>
                </template>
                </el-table-column>
            </el-table>
            <!---->
            <el-row  :span="24">
                <div class="pagination">
                    <el-pagination
                        @size-change="handleSizeChange"
                        @current-change="handleCurrentChange"
                        :current-page.sync="paginations.page_index"
                        :page-sizes="paginations.page_sizes"
                        :page-size="paginations.page_size"
                        :layout="paginations.layout"
                        :total="paginations.total">
                    </el-pagination>
                </div>
            </el-row>
            
        </div>
        <UserDialog :dialog="dialog" :formData="formData" @update="getReviewList"></UserDialog>
    </div>
</template>

<script>
// Dialog
import UserDialog from '../components/UserDialog'

export default {
    name:"reviewlist",
    data(){
        return {
            // 
            filterTableData:[],
            // 
            paginations:{
                page_index: 1,// 
                total: 0,//
                page_size:5,//
                page_sizes:[5,10,15,20],//
                layout:"total,sizes,prev,pager,next,jumper" //
            },
            //
            formData:{
                lab_id:"",
                lab_name:"",
                book_date:"",
                book_user:"",
                status:"",
                part:""
            },
            tableData:[],
            allTableData:[],
            // dialog
            dialog:{
                show:false,
                title:'',
                option:'edit'
            }
        };
    },
    computed:{
        user(){
            return this.$store.getters.user;
        }
    },
    created(){
        this.getReviewList();
    },
    methods:{
        getReviewList(){
            this.$axios.get(`/api/reviews/not/${this.user.username}`)
            .then(res=>{
                this.allTableData = res.data;
                this.filterTableData = res.data;
                // 
                this.setPaginations();
            }).catch(err=>{console.log(err)});
        },
        handleSizeChange(page_size){
            // size
            this.paginations.page_index = 1;
            this.paginations.page_size = page_size;
            this.tableData = this.allTableData.filter((item,index) => {
              return index < page_size   
            })
        },
        handleCurrentChange(page){
            //  =  *  - 1
            let index = this.paginations.page_size * (page - 1);
            //  + 1 
            let nums = this.paginations.page_size * page;
            // 
            let tables = [];
            //  [inde,nums),
            for (let i = index; i < nums; i++){
                if(this.allTableData[i]){
                    tables.push(this.allTableData[i]);
                }
            }
            // 
            this.tableData = tables;
        },
        setPaginations(){
            // 
            this.paginations.total = this.allTableData.length;//
            this.paginations.page_index = 1;//
            this.paginations.page_size = 5;//
            // 
            this.tableData = this.allTableData.filter((item,index) => {
              return index < this.paginations.page_size;    
            })
        },
        handleAgree(index,row){
            const ReviewFields = {
                review_user : this.user.username,
                review_date : row.book_date,
                lab_id: row.lab_id,
                book_date:row.book_date,
                book_part:row.part,
                book_user:row.book_user
            }
            this.$axios.post(`/api/reviews/agree/${row._id}`,ReviewFields)
            .then(res=>{
                this.$message({
                    type:"success",
                    message:""
                })
                this.getReviewList();
            })
        },
        handleRefuse(index,row){
            console.log(row._id);
            const refuseFields = {
                book_date: row.book_date,
                part:row.part
            }
            this.$axios.post(`/api/reviews/refuse/${row._id}`,refuseFields)
            .then(res=>{
                this.$message({
                    type:"success",
                    message:""
                })
                this.getReviewList();
            })
        }
    },
    components:{
        UserDialog
    }
}
</script>
<style scoped>
.fillContainer{
    width: 90%;
    height: 100%;
    padding-left: 100px;
    padding-top: 50px;
    box-sizing:border-box;
}

.btnRight{
    float: right;
}

.pagination{
    text-align: right;
    margin-top: 10px;
}
</style>